/* eslint-disable no-param-reassign */
import React, { createRef, useState } from 'react';
import { Row, Card, CardBody, FormGroup, Label, Spinner } from 'reactstrap';
import { Formik, Form, Field } from 'formik';
import { Wizard, Steps, Step } from 'react-albus';
import { connect } from 'react-redux';
import { useIntl } from 'react-intl';
import { Colxx } from '../../components/common/CustomBootstrap';
import IntlMessages from '../../helpers/IntlMessages';
import TopNavigation from '../../components/wizard/TopNavigation';
import BottomNavigation from '../../components/wizard/BottomNavigation';
import { FormikReactSelect } from '../../containers/form-validations/FormikFields';

const validateName = (value) => {
  let error;
  if (!value) {
    error = 'Please enter your name';
  } else if (value.length < 2) {
    error = 'Value must be longer than 2 characters';
  }
  return error;
};

const options = [
  { value: 'food', label: 'Food' },
  { value: 'beingfabulous', label: 'Being Fabulous', disabled: true },
  { value: 'reasonml', label: 'ReasonML' },
  { value: 'unicorns', label: 'Unicorns' },
  { value: 'kittens', label: 'Kittens' },
];

const CompleteRegistration = ({
  // history,
  // loading,

  error,
}) => {
  const intl = useIntl();

  console.log(error);
  // useEffect(() => {
  //   console.log(loading, error);
  //   if (error) {
  //     console.log(loading, error);
  //     NotificationManager.warning(
  //       error,
  //       'Registration Error',
  //       3000,
  //       null,
  //       null,
  //       ''
  //     );
  //   }
  // }, [loading, error]);

  const forms = [createRef(null), createRef(null), createRef(null)];
  const [bottomNavHidden, setBottomNavHidden] = useState(false);
  const [loading, setLoading] = useState(false);
  const [fields, setFields] = useState([
    {
      valid: false,
      name: 'name',
      value: '',
    },
    {
      valid: false,
      name: 'email',
      value: '',
    },
    {
      valid: false,
      name: 'password',
      value: '',
    },
  ]);

  const asyncLoading = () => {
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
    }, 3000);
  };

  const onClickNext = (goToNext, steps, step) => {
    if (steps.length - 1 <= steps.indexOf(step)) {
      return;
    }
    const formIndex = steps.indexOf(step);
    const form = forms[formIndex].current;
    const { name } = fields[formIndex];
    form.submitForm().then(() => {
      const newFields = [...fields];

      newFields[formIndex].value = form.values[name];
      newFields[formIndex].valid = !form.errors[name];
      setFields(newFields);

      if (!form.errors[name] && form.touched[name]) {
        goToNext();
        step.isDone = true;
        if (steps.length - 2 <= steps.indexOf(step)) {
          setBottomNavHidden(true);
          asyncLoading();
        }
      }
    });
  };

  const onClickPrev = (goToPrev, steps, step) => {
    if (steps.indexOf(step) <= 0) {
      return;
    }
    goToPrev();
  };

  // const onClick = (values) => {};

  return (
    <Row className="h-100">
      <Colxx xxs="12" md="8" className="mx-auto my-auto">
        <Card>
          <CardBody className="wizard wizard-default">
            <Wizard>
              <TopNavigation className="justify-content-center" disableNav />
              <Steps>
                <Step
                  id="step1"
                  name={intl.formatMessage({ id: 'user.fullname' })}
                  desc={intl.formatMessage({ id: 'user.fullname-desc' })}
                >
                  <div className="wizard-basic-step">
                    <Formik
                      innerRef={forms[0]}
                      initialValues={{
                        name: fields[0].value,
                      }}
                      onSubmit={() => {}}
                    >
                      {({ errors, touched }) => (
                        <Form className="av-tooltip tooltip-label-right">
                          <FormGroup>
                            <Label>
                              <IntlMessages id="user.fullname" />
                            </Label>
                            <Field
                              className="form-control"
                              name="name"
                              validate={validateName}
                            />
                            {errors.name && touched.name && (
                              <div className="invalid-feedback d-block">
                                {errors.name}
                              </div>
                            )}
                          </FormGroup>
                        </Form>
                      )}
                    </Formik>
                  </div>
                </Step>
                <Step
                  id="step2"
                  name={intl.formatMessage({ id: 'user.rank-skills' })}
                  desc={intl.formatMessage({ id: 'user.rank-skills-desc' })}
                >
                  <div className="wizard-basic-step">
                    <Formik
                      innerRef={forms[1]}
                      initialValues={{
                        email: fields[1].value,
                      }}
                      onSubmit={() => {}}
                    >
                      {({
                        setFieldValue,
                        setFieldTouched,
                        errors,
                        touched,
                      }) => (
                        <Form className="av-tooltip tooltip-label-right">
                          <FormGroup>
                            <FormikReactSelect
                              options={options}
                              onChange={setFieldValue}
                              onBlur={setFieldTouched}
                            >
                              {errors.reactSelect && touched.reactSelect ? (
                                <div className="invalid-feedback d-block">
                                  {errors.reactSelect}
                                </div>
                              ) : null}
                            </FormikReactSelect>
                          </FormGroup>
                          <FormGroup>
                            <FormikReactSelect
                              options={options}
                              onChange={setFieldValue}
                              onBlur={setFieldTouched}
                            >
                              {errors.reactSelect && touched.reactSelect ? (
                                <div className="invalid-feedback d-block">
                                  {errors.reactSelect}
                                </div>
                              ) : null}
                            </FormikReactSelect>
                          </FormGroup>
                          <FormGroup>
                            <FormikReactSelect
                              options={options}
                              onChange={setFieldValue}
                              onBlur={setFieldTouched}
                            >
                              {errors.reactSelect && touched.reactSelect ? (
                                <div className="invalid-feedback d-block">
                                  {errors.reactSelect}
                                </div>
                              ) : null}
                            </FormikReactSelect>
                          </FormGroup>
                          <FormGroup>
                            <FormikReactSelect
                              options={options}
                              onChange={setFieldValue}
                              onBlur={setFieldTouched}
                            >
                              {errors.reactSelect && touched.reactSelect ? (
                                <div className="invalid-feedback d-block">
                                  {errors.reactSelect}
                                </div>
                              ) : null}
                            </FormikReactSelect>
                          </FormGroup>
                          <FormGroup>
                            <FormikReactSelect
                              options={options}
                              onChange={setFieldValue}
                              onBlur={setFieldTouched}
                            >
                              {errors.reactSelect && touched.reactSelect ? (
                                <div className="invalid-feedback d-block">
                                  {errors.reactSelect}
                                </div>
                              ) : null}
                            </FormikReactSelect>
                          </FormGroup>
                        </Form>
                      )}
                    </Formik>
                  </div>
                </Step>

                <Step id="step4" hideTopNav>
                  <div className="wizard-basic-step text-center pt-3">
                    {loading ? (
                      <div>
                        <Spinner color="primary" className="mb-1" />
                        <p>
                          <IntlMessages id="wizard.async" />
                        </p>
                      </div>
                    ) : (
                      <div>
                        <h2 className="mb-2">
                          <IntlMessages id="wizard.content-thanks" />
                        </h2>
                        <p>
                          <IntlMessages id="wizard.registered" />
                        </p>
                      </div>
                    )}
                  </div>
                </Step>
              </Steps>
              <BottomNavigation
                onClickNext={onClickNext}
                onClickPrev={onClickPrev}
                className={`justify-content-between ${
                  bottomNavHidden && 'invisible'
                }`}
                prevLabel={intl.formatMessage({ id: 'user.previous-button' })}
                nextLabel={intl.formatMessage({ id: 'user.next-button' })}
              />
            </Wizard>
          </CardBody>
        </Card>
      </Colxx>
    </Row>
  );
};
const mapStateToProps = ({ authUser }) => {
  const { loading, error } = authUser;
  return { loading, error };
};

export default connect(mapStateToProps, {})(CompleteRegistration);
